import { Controller, Injection } from '@ali/4ever-cangjie';

export function setInjection(controller: Controller, key: string, data: Record<string, any>) {
  const { injections } = controller.value;
  const injection = Injection.create({
    key,
    data,
  });
  return controller.setInjections([
    ...injections.filter(inj => inj.key !== key),
    injection,
  ]);
}
